// fonts
$font-main: 'Montserrat', sans-serif;
$font-main2: 'Roboto', sans-serif;
$font-main3: 'Open Sans', sans-serif;
$font-secondary: 'Libre Caslon Display', serif;
$font-secondary2: 'Merriweather Sans', sans-serif;
$font-secondary3: 'Bebas Neue', sans-serif;
$font-secondary4: 'Libre Caslon Text', serif;

// colors
$color-primary: #000;
$color-primary2: #fff;
$color-primary3: #ffe4c4;
$color-primary4: #ffe4c4;
$color-primary5: #a98f6f;
$color-primary6: #797777;
$color-secondary: #fff;
$color-secondary2: #f08080;
$color-secondary3: #fafad1;
$gradient-bgd-main: linear-gradient(0, #fafad152, #8080806a);
